nav {
    background-color: #333; /* Background color */
    overflow: hidden; /* Clears floats */
    position: fixed; /* Fixed position at the top */
    top: 0; /* Position at the top */
    width: 100%; /* Full width */
    z-index: 1000; /* Ensures nav is above other content */
}

nav ul {
    list-style-type: none; /* No bullet points */
    margin: 0; /* Removes default margin */
    padding: 0; /* Removes default padding */
    text-align: center; /* Centers the links */
}

nav li {
    display: inline; /* Display links in a row */
}

nav a {
    display: inline-block; /* Display as block element */
    color: white; /* Text color */
    text-align: center; /* Center text */
    padding: 14px 16px; /* Padding for spacing */
    text-decoration: none; /* No underline */
}

nav a:hover {
    background-color: #111; /* Color on hover */
}
